@import "utilities/variables"
@import "./base/_typo.sass"
@import "./base/_reset.sass"



.mainFrame
  display: flex
  flex-direction: column
  justify-content: center
  align-items: center
  @media (max-width: $mediaQuerySize)
    width: 100vw
    overflow-x: hidden




///////////////////// Navbar

.sticky
  position: sticky
  width: 100%

.navbar
  display: flex
  justify-content: flex-start
  background: $colorBlacknoire
  min-height: 1rem
  padding: 16px 17px 13px 45px

  @media (max-width: $mediaQuerySize)
    justify-content: flex-end

.burger
  width: 2rem
  opacity: 1


.burgerIcon
  width: 100%
  height: 100%

.burgerIcon:hover
  content: url("../img/hoverBurger.svg")


///////////////////// Logo

.logoNewton
  color: $colorMercury
  font-family: $fontCrimsonPro
  font-size: calc(80px + 2.1vw)
  font-weight: 400
  padding: 2rem 0 0.2rem 0

  @media (max-width: $mediaQuerySize)
    padding: 0 0 0.6rem 0
    font-size: 4.7rem



///////////////////// inputArea

//@media (max-width: 706px)
    lo que pasa cuando supera el tamaño

.inputSection
  width: 45%
  min-width: $mediaQuerySize
  padding-bottom: 1.2rem
  font-size: 1.5rem
  @media (max-width: $mediaQuerySize)
    padding: 0 1rem 1.4rem 1rem
    width: 100%
    min-width: auto

.space
  padding-top: 10rem

.inputBar
  background: $colorMercury
  border-radius: 8px
  height: inherit
  width: 100%

.equal
  height: calc(100% - 0.9rem)

.equalImg
  height: 100%

.inputBarArea
  display: flex
  background: $colorMercury
  border-radius: 8px
  height: 3.4rem
  width: 100%
  align-items: center
  padding-right: 0.4rem


///////////////////// Keyboard
//Media queries para ir reduciendo tamaño del teclado


.keyBoardSection
  display: grid
  justify-content: center
  justify-items: center
  grid-template-columns: repeat(9, 2rem)
  gap: 0.35rem 2.2rem
  width: 67vw
  min-width: 27rem
  padding-top: 0.2rem
  @media (max-width: $mediaQuerySize)
    grid-template-columns: repeat(6,2rem)
    gap: 0.7rem 1.7rem
    width: 100%


.keyboardButton
  display: flex
  justify-content: center
  align-items: center
  font-family: $fontLibMath
  font-size: 1.4em
  height: 2.9rem
  width: 3.4rem
  border-radius: 2px
  background: $colorMercury
  transition: 0.3s
  @media (max-width: $mediaQuerySize)
    height: 2em
    width: 2.6em
    font-size: 1.2em

.keyboardButton:hover
  background: $colorClearOrange

.clearButton
  height: 100%
  width: 100%
  border-radius: 2px
  background: $colorClearOrange
  transition: 0.3s

.clearButton:hover
  background: $colorRZB

.customIcon
  height: 100%
  width: 100%

.cscAlign
  grid-column-start: 2
  @media (max-width: $mediaQuerySize)
    grid-column-start: auto

.derivAlign
  grid-column-start: auto
  @media (max-width: $mediaQuerySize)
    grid-column-start: 2



///////////////////// Result
//Media queries para ajustar a los bordes de la pantalla

.resultSection
  width: 37%
  min-width: 29rem
  padding: 4rem 0 1rem 0
  @media (max-width: $mediaQuerySize)
    padding: 3rem 1.5rem 0 1.5rem
    width: 100%
    min-width: auto

.resultBox
  display: flex
  justify-content: center
  align-items: center
  position: relative
  height: 28vh
  width: inherit
  padding: 2rem
  border-radius: 8px
  background: $colorMercury

.copyToClipboard
  width: 1.2em
  position: absolute
  right: 1em
  bottom: 20px
  @media (max-width: $mediaQuerySize)
    top: 1em

.resultBoxBackground
  padding: 2rem
  border-radius: 8px
  background: #5e5e61
  @media (max-width: $mediaQuerySize)
    padding: 1.1rem


///////////////////// Overlay Menu

.overlayCloseArea

.overlay
  display: flex
  flex-direction: column
  position: absolute
  top: 5rem
  left: 3%
  height: 50vh
  width: 40vh
  transition: 0.5s
  background: $colorBlacknoire
  border-radius: 8px
  @media (max-width: $mediaQuerySize)
    right: 0

.avatar
  padding: 2rem
  height: 23vh
  width: 23vh
  align-self: center

.overlayButton1
  color: $colorMercury
  background: $colorBlacknoire
  padding: 1.5rem
  font-size: 1.5rem
  transition: 0.4s

.overlayButton2
  color: $colorMercury
  background: $colorBlacknoire
  padding: 1.5rem
  font-size: 1.5rem
  transition: 0.4s

.overlayButton3
  color: $colorMercury
  background: $colorBlacknoire
  padding: 1.5rem
  font-size: 1.5rem
  transition: 0.4s

.overlayButton1:hover
  color: $colorBlacknoire
  background: $colorFadedOrange

.overlayButton2:hover
  color: $colorBlacknoire
  background: $colorClearOrange

.overlayButton3:hover
  color: $colorBlacknoire
  background: $colorRZB


///////////////////////////////

body
  background: $colorLack






